.statistics {
  padding: 20px;
  border-radius: 4px;
  text-align: center;
  background-color: #fff;
  box-shadow: 0px 0px 10px 0px rgba(229, 230, 235, 0.863);
  margin-bottom: 40px;
}

.title {
  font-size: 18px;
  margin-bottom: 10px;
}

.stat_list {
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin: 0;
}
