.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0;
  border-right: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  width: 100%;
  max-width: 200px;
  text-align: center;
}

.label {
  margin-bottom: 10px;
  font-size: 12px;
  color: #888;
}
.percentage {
  font-size: 24px;
  font-weight: 700;
  color: #000;
}
.item:hover,
.item:focus {
  cursor: pointer;
  box-shadow: inset 0 0 10px 0 rgba(0, 0, 0, 0.3);
}
